import React from 'react'
import { 
    MenuGroup, 
    MenuButton,
    StyledMenu,
    StyledMenuItem,
    Title,
    Line,
    StyledDoneIcon as DoneIcon
} from '@styles/stockMenu.style';
import stockMenu, {PRICE, priceMenuItemNames} from '@view/stockMenu.store';
import { observer } from 'mobx-react-lite';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import SortIcon from '@mui/icons-material/Sort';
import Stock from '@components/Stock';
// import { Container } from '@styles/blocks';
import { Container } from '@mui/material';
import sortOption from '@view/sortOption.store';

function StockMenuItems({menuName}) {
    function handleClickMenuItem(index) {
        sortOption.setSelectedId(index);
    }
    return (
        <React.Fragment>
            {
                priceMenuItemNames.map((name, index) => {
                    return (
                        <StyledMenuItem
                            key={index}
                            onClick={() => handleClickMenuItem(index)}
                        >
                            {name}{(index === sortOption.selectedId) ? <DoneIcon /> : null}
                        </StyledMenuItem>
                    )
                })  
            }
        </React.Fragment>
    )
}

function StockMenu({stocks}) {
    function handleClick(e) {
        stockMenu.openMenu(e.currentTarget);
    }
    function handleCloseMenu() {
        stockMenu.closeMenu();
    }
    if (stocks.length === 0)
        return (
            <Container>
                <h1>  </h1>
            </Container>
        )
  return (
    <div>
        <Container>
            <MenuGroup>
                <Title sx={{marginRight:'auto'}}><SortIcon/><span></span></Title>
                <Title sx={{marginRight:'15%'}}></Title>
                <MenuButton 
                    name={PRICE}
                    onClick={handleClick}
                >{priceMenuItemNames[sortOption.selectedId]}<KeyboardArrowDownIcon /></MenuButton>
            </MenuGroup>
            <StyledMenu
                anchorEl={stockMenu.priceButtonEl}
                open={Boolean(stockMenu.priceButtonEl)}
                onClose={handleCloseMenu}
            >
            <StockMenuItems menuName={PRICE}/>
            </StyledMenu>
        </Container>
        {
            stocks.map((stock, index, arr) => {
                    let isBoldBorder = false;
                    if (index !== 0 && arr[index - 1].category !== arr[index].category) 
                        isBoldBorder = true;
                    return (
                        <Stock isBoldBorder={isBoldBorder} key={index} {...stock} />
                    )
                }
            )
        }
        <Line />
    </div>
  )
}

export default observer(StockMenu);