import React from 'react'
import { ImgWrap } from '@styles/blocks';
import {
    Title,
    Price,
    Descr,
    StyledTechnic,
} from '@styles/technic.style';
function Technic({name, price, imgSrc, descr}) {
  return (
    <StyledTechnic>
        <ImgWrap
            width='250'
            height='250'
            src={imgSrc}
            alt='photo'
        />
        <Title sx={{textAlign: 'center', marginTop: '15px'}}>{name}</Title>
        <Descr>{descr}</Descr>
        <Price sx={{textAlign: 'center', marginTop: '15px'}}>  : {price} </Price>
    </StyledTechnic>
  )
}

export default Technic