import React from 'react'
// import { Container } from '@styles/blocks';
import { Container } from '@mui/material';
import technics from '@domain/technic.store';
import {
  StyledTechnics,
  TechnicsWrap
} from '@styles/technic.style';
import Technic from './Technic';
import {
  Title,
} from '@styles/technic.style';

function Technics() {
  return (
    <Container>
      <StyledTechnics> 
      <Title sx={{textAlign: 'center', margin: '35px'}}> </Title>
      <TechnicsWrap>
      {
          technics.data.map((technic, id) => {
            return (
              <Technic {...technic} key={id}/>
            )
          })
        }
      </TechnicsWrap>

      </StyledTechnics>
    </Container>
  )
}

export default Technics