import React from 'react'
import {
    Stock as StyledStock,
    PriceWrap,
    DescrText,
    PriceText,
    CompanyText,
    CompanyWrap,
    Wrap,
    TitleWrap,
    DescrWrap,
    ShortenerWrap,
    ShortenerText
} from '@styles/stockMenu.style'; 
// import { Container } from '@styles/blocks';
import {Box} from '@mui/material';
import { ImgWrap } from '@styles/blocks';
import {Container} from '@mui/material'

function Stock({
    price,
    descr,
    title,
    isBoldBorder,
    info,
    cubes,
    imgSrc
}) {
  return (
    <StyledStock isBoldBorder={isBoldBorder}>
        <Container>
            <Wrap>
                <TitleWrap>
                    <ImgWrap
                        width={200} 
                        height={200} 
                        src={imgSrc} 
                        alt={`logo`}
                    />
                    <Box marginLeft={'23px'}>
                        <CompanyWrap width='200px'>
                            <CompanyText>{title}</CompanyText>
                        </CompanyWrap>
                        <ShortenerWrap width='200px'>
                            <ShortenerText>{info}</ShortenerText>
                        </ShortenerWrap>
                    </Box>
                </TitleWrap>
                <DescrWrap>
                    <DescrText>{descr}</DescrText>
                </DescrWrap>
                <PriceWrap textAlign='right'>
                    <PriceText>{cubes}  {price} </PriceText>
                </PriceWrap>
            </Wrap>
        </Container>
    </StyledStock>
  )
}

export default Stock