import { makeAutoObservable, reaction } from "mobx";
import sortOption from '@view/sortOption.store';
import stockMenu from "@view/stockMenu.store";
import data from '@domain/stocks.data';

function getComparatorOfPrice(type) {
    switch (type) {
        case 1:
            return (a, b) => a.price - b.price;
        case 2:
            return (a, b) => b.price - a.price;
        default:
            return () => 0;
    }
}

class StocksStore {
    constructor() {
        makeAutoObservable(this);
        reaction(() => this._catalogStocks, (v) => {
            this.setCatalogCopy([...v]);
        })
        reaction(() => sortOption.selectedId, () => {
            stockMenu.closeMenu();
            const newV = [...this._catalogStocks];
            newV.sort(getComparatorOfPrice(sortOption.selectedId));
            this.setCatalogCopy(newV);
        })
    }
    _catalogStocks = {};
    catalogStocks = [];
    loadCatalog = () => {
        this._catalogStocks = data;
    }
    setCatalogStocks = (data) => {
        this._catalogStocks = data;
    }
    setCatalogCopy = (v) => {
        this.catalogStocks = v; 
    }
    filterCatalogStocks = (substr) => {
        const lcsubstr = substr.toLowerCase();
        const filteredStocksByName = [];
        for (let item of this._catalogStocks)
            if (item.title.toLowerCase().includes(lcsubstr) || item.info.toLowerCase().includes(lcsubstr))
                filteredStocksByName.push(item);
        this.catalogStocks = filteredStocksByName;
    }
}

export default new StocksStore();