import React from 'react'
import {
    StyledFooter,
    OfficeText,
    DescrText,
    OfficeTextWrap,
    DescrTextWrap,
    ButtonToTop,
    ButtonText,
} from '@styles/footer.style';
// import { Container } from '@styles/blocks';
import { Container } from '@mui/material';
import ArrowUpwardIcon from '@mui/icons-material/ArrowUpward';
import { Box } from '@mui/material';

function Footer() {
    function handleOnTop() {window.scrollTo({ top: 0, behavior: 'smooth' })}
  return (
    <StyledFooter>
        <Container sx={{position:'relative'}}>
            <ButtonToTop
                onClick={handleOnTop}
            >
                <ButtonText></ButtonText>
                <ArrowUpwardIcon sx={{fontSize: '50px', color: 'white', marginLeft: '15px'}}/>
            </ButtonToTop>
            <OfficeTextWrap>
                <OfficeText>: - </OfficeText>
            </OfficeTextWrap>
            <DescrTextWrap>
                <DescrText>:</DescrText>
            </DescrTextWrap>
            <Box marginTop='15px'>
                <DescrText>+7(917)-561-49-58, </DescrText>
            </Box>
            <Box marginTop='15px'>
                <DescrText>+7(916)-741-96-90, </DescrText>
            </Box>
        </Container>
    </StyledFooter>
  )
}

export default Footer;