import React from 'react'
import { StyledModal, Content } from '@styles/blocks';
import phoneModal from '@view/phoneModal.store';
import { observer } from 'mobx-react-lite';
import {
    NameText,
    PhoneText, 
    CloseButton, 
    StyledCloseIcon
} from '@styles/stockMenu.style';
import {Box} from '@mui/material';

function PhoneModal() {
  return (
    <StyledModal
        onClose={phoneModal.close}
        open={phoneModal.isOpen}
    >
        <Content>
            <NameText>:</NameText>
            <CloseButton
                onClick={phoneModal.close}
            ><StyledCloseIcon /></CloseButton>
            <Box textAlign={'center'}>
                <NameText></NameText>
            <PhoneText> : +7(917)-561-49-58</PhoneText></Box>
            <Box textAlign={'center'}>
                <NameText></NameText>
            <PhoneText> : +7(916)-741-96-90</PhoneText></Box>
        </Content>
    </StyledModal>
  )
}

export default observer(PhoneModal);