import colors from './colors';
import styled from '@emotion/styled';
import { styledOverride, textCreator, textWrapCreator } from './blocks';
import {
    Box,
    Button,
    Menu,
    MenuItem,
    Typography
} from '@mui/material';
import DoneIcon from '@mui/icons-material/Done';
import CloseIcon from '@mui/icons-material/Close';

export const NameText = textCreator(55,41,800);
export const PhoneText = textCreator(44,32,500);

export const MenuGroup = styled(Box)`
    position: relative;
    padding-top: 15px;
    padding-bottom: 15px;
    display: flex;
    justify-content: end;
`

export const MenuButton = styled(Button)`
    line-height: 26px;
    font-size: 23px;
    font-weight: 300;
    color: ${colors.dark};
    text-transform: none;
    ${(props) => (props.mRight) ? 'position: absolute;' : ''}
    right: ${(props) => (props.mRight) ? props.mRight + 'px' : 0};
`

export const StyledMenu = styledOverride(Menu, {
    '.MuiList-root': {
        paddingTop: 0,  
        paddingBottom: 0,
    },
    '.MuiPaper-root': {
        borderRadius: 29,
        backgroundColor: colors.bgcInfo,
    },
});

export const StyledMenuItem = styled(MenuItem)`
    background-color: ${colors.bgcInfo};
    font-size: 25px;
    line-height: 29px;
    font-weight: 300;
    padding-top: 23px;
    padding-bottom: 23px;
    border-bottom: ${({isBordered}) => (isBordered) ? '1px #494585 solid' : ''};
`

export const Stock = styled(Box)`
    ${({isBoldBorder}) => (isBoldBorder) ? `border-top: 4px ${colors.black} solid` : `border-top: 1px ${colors.black} solid`};
    padding-top: 10px;
    padding-bottom: 10px;
`

export const Title = styled(Typography)`
    line-height: 26px;
    font-size: 23px;
    font-weight: 300;
    display: flex;
`;

export const CloseButton = styled(Button)`
    color: ${colors.dark};
    position: absolute;
    right: 7px;
    top: 7px;
`

export const Line = styled(Box)`
    height: 1px;
    background-color: ${colors.dark};
`

export const StyledCloseIcon = styled(CloseIcon)`
    font-size: 59px;
`

export const Wrap = styled(Box)`
    display: flex;
    justify-content: space-between;
    align-items: center;
`

export const StockLogoWrap = styled(Box)`
    width: 93px;
    height: 93px;
`

export const DifferenceWrap = textWrapCreator(0,0,{mBottom: 21});
export const DescrWrap = styled(Box)`
    width: 500px;
`;

export const ShortenerWrap = textWrapCreator(0, 0, {mTop: 25});
export const ShortenerText = textCreator(36, 31, 300, 'rgba(45, 41, 110, 0.65)');
export const CompanyWrap = textWrapCreator(0, 0, {mRight: 10});
export const DescrText = styled(Typography)`
    font-weight: 300;
    font-size: 20px; 
`;
export const PriceText = textCreator(36, 31, 300, colors.dark);
export const CompanyText = textCreator(36, 31, 300, colors.dark);

export const TitleWrap = styled(Box)`
    display: flex;
    align-items: center;
`

export const PriceWrap = styled(Box)`
`

export const StyledDoneIcon =  styled(DoneIcon)`
    margin-left: 10px;
`