import React from 'react'
import {
    StyledHeader,
    LogoNumbers,
    LogoWrap,
    HeaderBar,
    StyledToolbar,
} from '@styles/header.style';
import {Button} from '@mui/material'
import PhoneEnabledIcon from '@mui/icons-material/PhoneEnabled';
import {observer} from 'mobx-react-lite';
import phoneModal from '@view/phoneModal.store';

function Header() {
  return (
    <StyledHeader>
        <HeaderBar position='static'>
            <StyledToolbar>
                <LogoWrap>
                    <LogoNumbers>Logo</LogoNumbers>
                </LogoWrap>
                <Button
                    onClick={phoneModal.open}
                >
                    <PhoneEnabledIcon sx={{fontSize: '93px', color: 'black'}}/>
                </Button>
            </StyledToolbar>
        </HeaderBar>
    </StyledHeader>
  )
}

export default observer(Header);