import colors from './colors';
import styled from '@emotion/styled';
import { textCreator } from './blocks';
import {
    Box,
} from '@mui/material';

export const StyledTechnics = styled(Box)`
    margin-top: 26px;
    margin-bottom: 26px;
    width: 100%;
    row-gap: 20px;
`

export const TechnicsWrap = styled(Box)`
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    row-gap: 30px;
`

export const StyledTechnic = styled(Box)`
    display: flex;
    width: 290px;
    flex-direction: column;
    align-items: center;
    margin-top: 30px;
    padding: 15px;
    border: 1px ${colors.dark} solid;
    border-radius: 26px;
`

export const Title = textCreator(51, 46, 800);
export const Price = textCreator(33, 30, 400);
export const Descr = textCreator(34, 24, 400);